import { createStore, applyMiddleware } from "redux";
import thunk from "redux-thunk";
import rootReducer from "./reducers/index";
import { secureStroge, storgeKeys } from "./utils";
import { composeWithDevTools } from "redux-devtools-extension";

const loadState = () => {
  try {
    const serializedUserAuth = secureStroge.get(storgeKeys.userAuth);
    if (serializedUserAuth === null) {
      return undefined;
    }
    return { auth: JSON.parse(serializedUserAuth) };
  } catch (err) {
    return undefined;
  }
};

//  reducer  Store Store  reducer 
const store = createStore(
  rootReducer,
  loadState(),
  // Debug  store 
  composeWithDevTools(
    //  middleware  applyMiddleware, ex: applyMiddleware(middleware1, middleware2, ...)
    //  enhancer  createStore
    applyMiddleware(thunk)
  )
);

let preUserAuth;
store.subscribe(() => {
  const { auth } = store.getState();
  if (preUserAuth !== auth) {
    secureStroge.set(storgeKeys.userAuth, JSON.stringify(auth));
    preUserAuth = auth;
  }
});

export default store;